body{
 font-family: Arial, Helvetica, sans-serif;
}
.navbar{
    background-color: brown;
   
}
.navbar h3{
    margin: 0;
}
.searchbar input{
    padding: 6px 3px 6px 3px;
    border-radius: 4px;
    border: none;
}
.create-searchBar{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between ;
    margin: 5px 0px 0px 0px;
    padding: 10px 30px 10px 30px;
    background: pink;
}
.invoice-display table{
    width: 100%;
}
.invoice-display tr,th,td{
    background: rgb(224, 156, 156);
}
.invoice-heading{
    color: black;
    background-color: red;
    padding: 4px;
    margin-top: 0;
}
.invoice-popupform{
    display: flex;
    flex-wrap: wrap;
    background-color: aqua;
    flex-direction: column;
    width: 90%;
    margin: auto;
    margin-top: 24px;
}
.main-invoice{
    display: flex;
    flex-direction: column;
}
.labels{
    display: flex;
    justify-content: center;
    gap: 20px;
}
.add-btn{
    display: flex;
    margin-right: 20px;
    justify-content: end;
}
.itemsform{
 display: flex;
 flex-direction: column;
 flex-wrap: wrap;
}
.itemform-input{
 display: flex;
 flex-wrap: wrap;
 gap: 10px;
 justify-content: center;
 margin-bottom: 20px;
}
.display-itemsform{
 background-color: aqua;
}
.display-itemsform table{
 width: 100%;
}
.total-display{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-right: 50px;
    margin-top: 20px;
    align-items: end;
}
.save-cancel-btn{
   margin: 1px 1px 5px 5px;
   display: flex;
   gap: 20px;
}
.button-style{
    border: none;
    background: brown;
    color: white;
    padding: 6px 10px 6px 10px;
    border-radius: 4px;
}